import React, { Component } from 'react';
import '../../styles/blog.css';

const blogs = [
    {
        image: 'assets/images/2.jpg',
        title: 'MÙA DỊCH COVID-19, ABE SHOES GIẢM GIÁ CỰC MẠNH ĐẾN 60% VÀ FREE SHIP 100%',
        description: 'Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore...'
    }, {
        image: 'assets/images/3.jpg',
        title: 'CÁCH CHỌN GIÀY PHÙ HỢP VỚI CÁ TÍNH CỦA BẠN VÀ XU HƯỚNG NGÀY NAY',
        description: 'Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore...'
    }
];

function SingleBlog(props) {
    return (
        <div class="blog">
            <img className="blog-image" src={props.image} />
            <div className="blog-text">
                <h3>{props.title}</h3>
                <p>{props.description}</p>
                <a href="#">XEM THÊM <img src="assets/icons/blog-arrow.png" /></a>
            </div>
        </div>
    );
}

class Blog extends Component {
    render() {
        return (
            <div class="blog-container">
                { blogs.map(item =>
                    <SingleBlog image={item.image} title={item.title} description={item.description} />    
                )}
            </div>
        );
    }
};

export default Blog;