import React, { Component } from 'react';
import '../../styles/features.css';

const features = [
    {
        image: 'assets/icons/support.png',
        title: 'HỖ TRỢ 24/7',
        eng: '24/7 Support',
        sub: 'Tư vấn nhanh chóng, tận tình'    
    }, {
        image: 'assets/icons/authentic.png',
        title: 'CAM KẾT CHÍNH HÃNG',
        eng: '100% Authentic',
        sub: 'Hoàn tiền 200% nếu phát hiện hàng giả'    
    }, {
        image: 'assets/icons/delivery.png',
        title: 'GIAO HÀNG HỎA TỐC',
        eng: 'Express Delivery',
        sub: 'Thời gian nhận hàng từ 2-3 ngày'    
    }
]

class Features extends Component {
    render() {
        const result = features.map(item =>
            <div className='feature'>
                <img src={item.image} />
                <h2>{item.title}</h2>
                <h5>{item.eng}</h5>
                <p>{item.sub}</p>
            </div>    
        );
        return (
            <div className='features-container'>
                {result}
            </div>
        );
    }
};

export default Features;