import React, { Component } from 'react';

class MailRegister extends Component {
    render() {
        return(
            <div className="mail-register-container">
                <div className="mail-register">
                    <img src="assets/icons/mail.png" />
                    <strong>NHẬN THÔNG BÁO HOT</strong>
                    <div className="input-email">
                        <input id="emailRegister" placeholder="Nhập email nhận tin" type="email" />
                        <button>ĐĂNG KÍ</button>
                    </div>
                    <span>Và nhận ngay <strong>VOUNCHER 100K</strong></span>
                </div>
            </div>
        );
    }
}

export default MailRegister;