import React, { Component, PropTypes, Fragment } from 'react';
import '../../styles/slider.css';

class Dot extends Component {
    constructor(props) {
        super(props);
        this.handleClick = this.handleClick.bind(this);
    }

    handleClick() {
        console.log(this.props.counter);
        this.props.updateDisplay(this.props.counter);
    }

    render() {
        return(
            <li><span className={this.props.data} onClick={this.handleClick} /></li>
        );
    }
}

class Slider extends Component {
    static defaultProps = {
        dots: true,
        infinite: true,
        slidesToScroll: 1,
        slidesToShow: 1,
        childWidth: 390,
        outerDim: {
            width: 490,
            height: 390
        },
        innerDim: {
            width: 390,
            height: 390
        }
    }
    
    constructor(props) {
        super(props);
        this.state = {
            currentIndex: 0,
            counter: 0,
            transform: '',
            n: this.props.children.length,
            prevDisplay: {},
            nextDisplay: {}
        }
        this.debug = this.debug.bind(this);
        this.prevClick = this.prevClick.bind(this);
        this.nextClick = this.nextClick.bind(this);
        this.updateButton = this.updateButton.bind(this);
        this.updateSpan = this.updateSpan.bind(this);
    }

    debug() {
        const { children } = this.props;
        console.log(children[0]);
    }

    updateButton(counter) {
        if (counter == 0) {
            if (!this.props.infinite) {
                this.setState({
                    prevDisplay: {visibility: "hidden"}
                })
            }
        }
        else {
            this.setState({
                prevDisplay: {visibility: "visible"}
            })
        }
        
        if (counter == (this.state.n - this.props.slidesToShow) / this.props.slidesToScroll) {
            if (!this.props.infinite) {
                this.setState({
                    nextDisplay: {visibility: "hidden"}
                })
            }
        }
        else {
            this.setState({
                nextDisplay: {visibility: "visible"}
            })
        }
    }

    componentDidMount() {
        if (!this.props.infinite) {
            this.updateButton(0);
        }
    }

    prevClick() {
        console.log(this.props);
        const {slidesToScroll, childWidth, slidesToShow} = this.props;
        let {counter, n} = this.state;
        var value;
        if (counter > 0) {
            counter--;
        }
        else {
            counter = (n - slidesToShow) / slidesToScroll;          
        }
        value = counter * childWidth * slidesToScroll;
        this.setState({
            counter: counter,
            transform: 'translateX(-' + value +'px)'
        });
        this.updateButton(counter);
        console.log(this.state);
    }

    nextClick() {
        const {slidesToScroll, childWidth, slidesToShow} = this.props;
        let {counter, n} = this.state;
        var value;
        if (counter < (n - slidesToShow) / slidesToScroll) {
            counter++;
            value = counter * childWidth * slidesToScroll;
        }
        else {
            counter = 0;
            value = 0;
        }
        this.setState({
            counter: counter,
            transform: 'translateX(-' + value +'px)'
        });
        this.updateButton(counter);
        console.log(this.state);
    }

    updateSpan(counter) {
        this.setState({
            counter: counter,
            transform: 'translateX(-' + this.props.childWidth * this.props.slidesToScroll * counter +'px)'
        })
        this.updateButton(counter);
    }

    render() {
        const {
            dots,
            infinite,
            slidesToShow,
            slidesToScroll,
            thisWidth,
            thisHeight,
            children,
            childWidth,
            childHeight,
            outerDim,
            innerDim
        } = this.props;

        console.log(children);

        const {
            prevDisplay,
            nextDisplay,
            counter
        } = this.state;

        let childrenClone = [];
        const n = this.state.n;
        function clone() {
            let id;
            for (let i = 0; i < n; i++) {
                childrenClone.push({ [i] : children[i] });
            }
            for (let i = 0; i < n - slidesToShow; i++) {
                    childrenClone.push( { [i + n]: children[i] } )
            }
            for (let i = n - 1; i >= n - slidesToShow; i--) {
                    childrenClone.unshift( { [i - n]: children[i] } );
            }

            console.log(childrenClone);
        };

        const ul = (dots) => {
            if (dots) {
                let spans = [], name = "";
                for (let i = 0; i < (n - slidesToShow) / slidesToScroll + 1; i++) {
                    name = "span" + i;
                    if (i === counter) name += " active";
                    spans.push(name);
                }
                return (
                    <ul>
                        {spans.map((data, index) => <Dot updateDisplay={this.updateSpan} counter={index} data={data} />)}
                    </ul>
                )
            }    
        }
        
        return(
            <div className="slider" style={outerDim}>
                <button type="button" className="prev" onClick={this.prevClick} style={prevDisplay}>PREIVOUS</button>
                <div className="slider-frame" style={innerDim}>
                    <div className="slider-container" style={{transform: [this.state.transform]}}>
                        {children}
                    </div>
                </div>
                <button type="button" className="next" onClick={this.nextClick} style={nextDisplay}>NEXT</button>
                {ul(dots)}
            </div>
        );
    }
}

export default Slider;