import React, { Component } from 'react';
import '../../styles/navbar.css';

const items = [
    {text: 'NAM', href: '#'},
    {text: 'NỮ', href: '#'}, 
    {text: 'TRẺ EM', href: '#'}, 
    {text: 'THƯƠNG HIỆU', href: '#'}, 
    {text: 'TRẢ GÓP', href: '#'}, 
    {text: 'LIÊN HỆ', href: '#'}, 
    {text: 'ĐĂNG NHẬP', href: '#'}
];

class Navbar extends Component {
    render() {
        return (
            <header>
                <nav class="navbar">
                <img src="assets/icons/logo.png" id="logo"/>
                <div class="brand">
                    <h1>ABE SHOES</h1>
                    <p>Đồng hành cùng bạn</p>
                </div>
                <ul>
                    { items.map(item => <li><a href={item.href}>{item.text}</a></li>) }
                </ul>
                <div class="your-cart">
                    <img src="assets/icons/cart.png" />
                    <div>GIỎ HÀNG</div>
                    <span id="cart-number">0</span>
                </div>
                <div class="search">
                    <input />
                    <img src="assets/icons/search.png" />
                </div>
            </nav>
            </header>
        );
    }
};

export default Navbar;